@import "./layouts/variable";
.articleCard {
  position: relative;
  width: 340px;
  height: 340px;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  align-items: flex-end;
  padding: 20px;
  &_img {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.allCards {
  position: relative;
  z-index: 0;
  background-color: $supplementary-color;
  width: 100%;
  margin: 40px 0;
  padding: 50px;
  border-radius: 25px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
  a {
    position: relative;
    z-index: 5;
    color: $secondary-color;
    text-decoration: none;
    font-size: 18px;
    font-weight: 700;
  }
}

.innerHome {
  padding: 0px 50px;
  &_picture {
    border-radius: 15px;
    background-color: rgba(0, 0, 0, 0.4);
  }
}

@media all and (max-width: 757px) {
  .innerHome {
    padding: 0 20px;
  }
  .allCards {
    background: $secondary-color;
    padding: 0;
    flex-direction: column;
  }
  .articleCard {
    width: 100%;
    height: 250px;
  }
}
