@import "./layouts/variable";

html,
body {
  height: 100%;
}

.innerAbout {
  width: 100%;
  padding: 0px 50px;
  display: flex;
  flex-direction: column;
  min-height: calc(100vh - 317px);

  section {
    width: 80%;
    margin: 20px auto;
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    gap: 20px;
    flex: 2;
  }
}

@media all and (max-width: 757px) {
  .innerAbout {
    padding: 0 20px 20px 20px;
    gap: 20px;
    section {
      width: 100%;
      justify-content: space-between;
      gap: 17px;
      margin: 0;
      flex: 1;
    }
    .picture {
      flex: 1;
      min-height: 111px;
    }
  }
}
